import React, { Component } from "react";
import Modal from "@material-ui/core/Modal";
import "./ResumeSourceCustomFilter.scss";
import SelectInput from "./SelectInput/SelectInput";
import CustomizedInputs from "./CustomizedInputs/CustomizedInputs";
import CloseCommentIcon from "../../assets/images/Icon ionic-ios-close-circle.svg";
import Button from "../Controls/Button/Button";
import DeleteIcon from "@material-ui/icons/Delete";

class Resumesorcecustomfilter extends Component {
  render() {
    const mapList = [];
    const { values, countFilter, optionsName, optionsCondition } = this.props;
    for (let index = 0; index < countFilter; index++) {
      mapList.push(index);
    }

    return (
      <Modal
        open={this.props.open}
        className='ResumeSorceCustomFilterModal'
        onClose={this.props.close}
      >
        <div className='filterMainDiv'>
          <div className='HeaderFilter'>
            Added details Filter{" "}
            <img
              src={CloseCommentIcon}
              alt='close'
              onClick={this.props.close}
            />
          </div>
          {mapList &&
            mapList.length > 0 &&
            mapList.map((el) => {
              return (
                <div className='InputDiv'>
                  <SelectInput
                    placeholder='Select Name'
                    className={"CustomResumeSourceFilterDropDown"}
                    onChange={(e) => this.props.onAddName(e, el)}
                    options={optionsName}
                    value={
                      values && values[el] && values[el].name
                        ? values[el].name
                        : null
                    }
                  />
                  <SelectInput
                    placeholder='Select Condition'
                    className={"CustomResumeSourceFilterDropDown"}
                    onChange={(e) => this.props.onAddCondition(e, el)}
                    options={optionsCondition}
                    value={
                      values && values[el] && values[el].condition
                        ? values[el].condition
                        : null
                    }
                  />
                  <div className='CustomResumeSourceFilterInput'>
                    <CustomizedInputs
                      className='inputArea'
                      placeholder='Enter the Value'
                      value={
                        values && values[el] && values[el].value
                          ? values[el].value
                          : null
                      }
                      onChange={(e) => this.props.onAddValue(e, el)}
                    />
                  </div>
                  <div>
                    <DeleteIcon
                      style={{ color: "red", cursor: "pointer" }}
                      onClick={() => this.props.onDelete(el)}
                    />
                  </div>
                </div>
              );
            })}
          <div className='AddMoreBtn'>
            <Button
              width='150px'
              height='34px'
              borderRadius='4px'
              className='res-btn'
              //   disabled={optionsName.length === countFilter ? true : false}
              onClick={this.props.onCountIncrease}
            >
              Add More
            </Button>
            <Button
              width='150px'
              height='34px'
              borderRadius='4px'
              className='res-btn'
              marginLeft='10px'
              disabled={values.length === 0}
              onClick={this.props.filterApply}
            >
              Filter
            </Button>
          </div>
        </div>
      </Modal>
    );
  }
}

export default Resumesorcecustomfilter;
