import * as JobDescriptionAction from "../../store/JobDescription/JobDescriptionAction";
import * as Actions2 from "../../store/Resumesource/resumesourceAction";
import * as ScheduleActions from "../../store/Schedule/scheduleAction";
import { bindActionCreators } from "redux";
